.ribbon {
    background-color: rgb(28, 76, 138);
    overflow: hidden;
    display: flex;
    justify-content: flex-end;
  }
  
  .ribbon a {
    float: left;
    display: block;
    color: white;
    text-align: center;
    padding: 6px 8px;
    text-decoration: none;
    position: relative;
  }
  
  .ribbon a:hover {
    background-color: #ddd;
    color: black;
  }
  