.header {
    display: flex;
    align-items: center;
    background-color: white;
    color: black;
    padding: 15px;
  }
  
  .header img {
    height: 80px;
    margin-right: 10px;
  }
  
  .header button,
  .header a {
    background-color: white;
    color: rgb(28, 76, 138);
    padding: 8px 12px;
    margin-right: 5px;
    text-decoration: none;
    cursor: pointer;
    border-radius: 10px;
    text-align: right;
    font-size: large;
  }
  
  .header button:hover,
  .header a:hover {
    background-color: white;
    color: rgb(28, 76, 138);
  }
  
  .header button {
    border: 1px solid rgb(28, 76, 138);
  }
  
  .search-container {
    margin-left: auto;
    display: flex;
    align-items: center;
  }
  
  .search-input {
    padding: 8px;
    border: 1px solid #ddd;
    border-radius: 5px;
    margin-right: 5px;
  }
  
  .search-button {
    background-color: rgb(28, 76, 138);
    color: white;
    padding: 8px 12px;
    border: none;
    border-radius: 5px;
    cursor: pointer;
  }
  